@import "./global.scss";

.app{
  
  .btn3{
    position:fixed;
    
        right:100px;
        top :18px;
        z-index:999;
        border-radius: 10px;
        user-select: none;
        .switch {
          position: relative;
          display: inline-block;
          width: 60px;
          height: 34px;
          -webkit-tap-highlight-color: transparent;
        }
        
        .switch input {
          opacity: 0;
          width: 0;
          height: 0;
        }
        
        .slider {
          position: absolute;
          cursor: pointer;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: #474747;
          -webkit-transition: 0.4s;
          transition: 0.4s;
        }
        
        .slider:before {
          position: absolute;
          content: "";
          height: 26px;
          width: 26px;
          left: 30px;
          bottom: 4px;
          box-shadow: inset -7px 0 0 2px #ffe600;
          background-color: #474747;
          
          -webkit-transition: 0.4s;
          transition: 0.4s;
        }
        
        input:checked + .slider {
          background-color: #474747;
          
        }
        
        
        
        input:checked + .slider:before {
          -webkit-transform: translateX(-26px);
          -ms-transform: translateX(-26px);
          transform: translateX(-26px);
          background-color: #ffe600;
        }
        
        .slider.round {
          border-radius: 17px;
        }
        
        .slider.round:before {
          border-radius: 50%;
        }
        
        
  }
  
    
    

    .bkg{
    // $bg-url1: "1.jpg";
    // $bg-url2: "2.jpg";
    height: 100vh;

    $bg-width: 50px;
    $bg-height: 50px;
  
  @keyframes bg-scrolling-reverse {
    100% {
      background-position: $bg-width $bg-height;
    }
  }
  @keyframes bg-scrolling {
    0% {
      background-position: $bg-width $bg-height;
    }
    
  }
   //background: url(1.jpg) repeat 0 0;

  animation: bg-scrolling-reverse 0.6s infinite; /* IE 10+ */
  animation-timing-function: linear;
  transition: all 0.6s ease;
}
    .sections{
        width: 100%;
        height: 100vh ;
        
    
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            
            padding-top: 70px;
            width: 100vw;
            height: 100vh;
            scroll-snap-align: start;
        }
        .cont{
                padding-top: 0px;

        }

    }
}
