@import "../../global.scss";

.contact{
    // background-color: #a57eff;
    padding: 0;
    height:calc(100% - 70px);
    position: relative;
    .text{
        overflow: hidden;
        user-select: none;
        
        display: flex;
        align-items: center;
        justify-content: center;
        font-size:calc(40px + 3vw);
        font-style: italic;
        font-weight: 500;
        -webkit-text-stroke: 1.2px $white;
        -webkit-text-fill-color: rgba(0, 0, 0, 0);
        transition: all 1.5s ease;
        }
    .Eform{
        margin-top: 40px ;
        overflow-x:hidden ;
        width: 80%;
        height: 80%;
        color: rgb(255, 255, 255);
        background-color: rgba(109, 109, 109, 0.096);
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        border-radius: 20px;
        padding: 20px;
        padding-top:10px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 26px;
        position:absolute;
        align-items: center;
        padding: 20px 20px 20px 20px;
        text-align: center;
        .input100{
            width: calc(100px + 50vw);
            height: 50px;
            font-size: calc(17px + 0.5vw);
            border-radius: 10px;
            border: solid rgb(255, 255, 255) 1px;
            color:rgb(0, 0, 121);
            
           margin-bottom: 10px;
           margin-top: 5px;
        }
        .input100:focus, input:focus{
            outline: rgb(0, 26, 99) 2px;
            outline-style: solid;
        }
        .message{
            height: 20vh;
            resize: none;
            &::-webkit-scrollbar{
                width: 10px;
                //background-color: rgb(0, 11, 71);
            }
            &::-webkit-scrollbar-thumb {
                direction:rtl;
                border-radius: 5px;
                background: rgb(0, 72, 150);
              }
        
            > *{
                border-radius: 5px;
                padding-top: 20px;
                
                scroll-snap-align: start;
            }
        }
        .sumbit{
            width: calc(100px + 50vw);
            height: 50px;
            border-radius: 30px;
            border: none;
            color:rgb(255, 255, 255);
            font-size: 20px;
           background-color: rgb(201, 0, 0);
           &:hover{
               background-color:rgb(109, 0, 0);
           }
           margin-bottom: 10px;
        }
        .row{
            color:$white;
        }
    }
}