@import "../../global.scss";


.intro{
  
  

  display: flex;
    .btext{
      
      overflow: hidden;
      user-select: none;
      font-size: 110px;
      font-style: italic;
      font-weight: 500;
      -webkit-text-stroke: 1.5px  $white ;
      -webkit-text-fill-color: rgba(0, 0, 0, 0);
      transition: all 1.5s ease;
        flex:wrap;
      
        }

        .left{
          flex: 0.5;
          
          overflow: hidden;
          .imgContainer{
            height: 1200px;
            width: 1200px;
            background-color:$red ;
            -webkit-text-stroke:0;
            -webkit-text-fill-color: white;
            border-radius:50% ;
            display:flex ;
            align-items: center;
            justify-content: center;
            float: left;
            font-size: 45vh;
            float: right;
            margin-top: 10px;
            
        }
        }
        .right{
          flex:0.5;
          background-color: rgba(53, 77, 155, 0);
          padding-top: 70px;
          margin-left:150px ;
        }
       
}
