@import "../../global.scss";

.portfolio{
    user-select: none;
    //background-color: #fd91a1;
    display: flex;
    flex-direction: column;
    align-items: center;
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
       
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow: hidden;
        
        .item{
            width: 300px;
            height: 190px;
            margin: 30px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            position:relative;
            cursor:pointer;
            box-shadow: 0 0 11px rgb(33, 33, 33);
            border-radius: 30px;
            border: 2px solid $white;
            transition: all 0.5s ease;
            
            h3{
                position :absolute;
                top:20%;
                font-size: 25px;
                font-style: italic;
                font-weight: 500;
                color:  rgba(255, 255, 255, 0);
                transition: all 0.6s ease;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                
                               
            }
    
            &:hover{
                
                transform: scale(1.05, 1.05);
                img{
                    filter: brightness(50%);
                    transition: all 1s ease;
                }
                
                h3{     
                    color:  rgb(255, 255, 255);
                    
                }
            }
            
        }

    }
    .txt{
        overflow: hidden;
        user-select: none;
        font-size: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-style: italic;
        font-weight: 500;
        -webkit-text-stroke: 1.2px $white;
        -webkit-text-fill-color: $black ;
        transition: all 1.5s ease;
        }
}
